import React, { Component } from "react";
import logo from "../images/SA_Co_Shield-1.png";
import { Navbar, Nav, Button } from "react-bootstrap";
export default class NavbarStrap extends Component {
  state = {
    color: "",
    show: false,
  };

  listenScrollEvent = (e) => {
    if (window.scrollY > 10) {
      return this.setState({ color: "black", show: true });
    } else if (window.scrollY < 10) {
      return this.setState({ color: "", show: false });
    }
  };

  componentDidMount() {
    window.addEventListener("scroll", this.listenScrollEvent);
  }
  render() {
    return (
      <div>
        <Navbar
          fixed="top"
          style={{ backgroundColor: this.state.color }}
          expand="lg"
        >
          <Navbar.Brand href="#home">
            <img
              src={logo}
              height="90"
              width="90"
              className="d-inline-block align-top"
              alt="React Bootstrap logo"
            />
          </Navbar.Brand>
          <Navbar.Toggle
            style={{ color: "#ffff" }}
            aria-controls="basic-navbar-nav"
          />
          <Navbar.Collapse style={{ color: "#ffff" }} id="basic-navbar-nav">
            <Nav className="mr-auto">
              <Nav.Link style={{ color: "#ffff" }} href="#home">
                {this.state.show ? "SHOP" : ""}
              </Nav.Link>
              <Nav.Link style={{ color: "#ffff" }} href="#link">
                {this.state.show ? "SUMMER GEAR" : ""}
              </Nav.Link>
              <Nav.Link style={{ color: "#ffff" }} href="#home">
                {this.state.show ? "PACK DEALS" : ""}
              </Nav.Link>
              <Nav.Link style={{ color: "#ffff" }} href="#link">
                {this.state.show ? "WHOLESALE / CUSTOM GEAR" : ""}
              </Nav.Link>
              <Nav.Link style={{ color: "#ffff" }} href="#home">
                {this.state.show ? "EXPLORE SA" : ""}
              </Nav.Link>
            </Nav>
            <Button
              style={{
                color: "#ffff",
                backgroundColor: "#343a40",
                border: "solid 1px #343a40",
              }}
            >
              LOGIN
            </Button>
            <Button style={{ backgroundColor: "#30b7d4" }}>
              CART / $0.00 <i class="fas fa-shopping-cart"></i>
            </Button>{" "}
          </Navbar.Collapse>
        </Navbar>
      </div>
    );
  }
}
